<template>
    <div>
       <div id="window" >
         <div id="list" @click="move">

           <img src="../../static/end.jpg" id="end"/>
           <img src="../../static/race.jpg" id="race"/>
           <img src="../../static/start.jpg" id="start"/>
         </div>
         <img src="../../static/plane.jpg" id="plane" />
       </div>
       <button @click="start">{{startOrStop}}</button>
    </div>
</template>

<script>
    export default {
        name: "Plane",
        data () {
          return {
            startOrStop:'START',
            direction:0,
            counter:0,
            ready:0,
            timer:0
          }
        },
        methods:{
          move:function () {
            var n=parseInt($("#list").css("top"))

             if(n<400){
               $("#list").animate({"top":n+10},10,function(){

               })
             }else{
               clearInterval(this.timer)
               alert("起飞任务完成")
               this.startOrStop='START'
               $("#list").css("top","-100px")
             }
          },

          start:function () {

           if(this.startOrStop=='START'){
             this.startOrStop='STOP'
             this.timer=setInterval(
               function () {
                 var n=parseInt($("#list").css("top"))

                 if(n>-100){
                   n=n-1
                   $("#list").css("top",n.toString()+"px")
                 }else {
                   $("#list").css("top","-100px")
                 }
               },30
             )
           }else{
             this.startOrStop='START'
             clearInterval(this.timer)
             $("#list").css("top","-100px")
           }
          }
        }
    }
</script>

<style scoped>
    #window{
      height: 500px;
      width: 200px;
      position: relative;
      margin: 0 auto;
      border: solid 1px red;
      overflow: hidden;
    }
    #list{
      position: absolute;
      top: -100px;
      width: 10000px;
      height: 300px;
    }
    #list img{
      display: block;
    }
    #plane{
      position: absolute;
      top: 450px;
      left: 80px;
      z-index: 104;
      height: 40px;
      width: 40px;
    }
    #end{
      height: 50px;
      width: 200px;
    }
    #start{
      height: 50px;
      width: 200px;
    }
    #race{
      height: 500px;
      width: 200px;
    }
</style>



